html, body{
    margin: 0;
    padding: 0;
    height: 100%;
    box-sizing: border-box;
}

header{

}

header ul{
    list-style-type: none;
    display: flex;
    margin: 0;
    padding: 0;
    border-bottom: 3px solid gray;
}

header ul > li{
    padding: 15px 15px;
    color: #000;
    font-size: 20px;
}


.NavLink{
    color: #fff;
    text-decoration: none;
    font-weight: 700;
    padding-right: 10px;
}
.NavLink:hover{
    color: #03a9f4;
    text-decoration: none;
}

.NavLink--active{
    color: #ff9800;
    font-weight: bold;
}

.formInput{
    border: 1px solid #999;
    padding: 15px;
    margin: 30px auto;
    width: 500px;
    border-radius: 3px;
}